
.window {
    height: 520px;
    width: 350px;
    background-color: #C1C0C1;
    border-top: #FFFFFF 3px solid;
    border-left: #FFFFFF 3px solid;
    border-bottom: #000000 3px solid;
    border-right: #000000 3px solid;
  }

  button{
  
    position: absolute;
    left: 93.5%;
    top: 3px;
    height: 20px;
    width: 20px;
    background-color: #C1C0C1;
    border-top: #FFFFFF 3px solid;
    border-left: #FFFFFF 3px solid;
    border-bottom: #000000 3px solid;
    border-right: #000000 3px solid;
    text-align: center;
    font-size: 10px;
    padding: 0px;
  }

  .thin{
    margin-top: 5px;
  }
  
  .header{
    padding-top: 0px;
  }

  .skills{
    line-height: 120%;
  }
  .skills p{
    margin-top: 0;
    margin-bottom: 0;
  }
  .skills h2{
    padding-top: 0px;
  }
  
  .app {
    background-color: #C3C3C3;
    height: 95%;
    width: 100%;
    position:absolute;
    color: #000000;
}

  .title {
    margin: 0;
    top: 50%;
    color: #FFFFFF;
    position: relative;
    top: 50%;
    padding-left: 3px;
  }

  .banner {
    display: flex;                  /* establish flex container */
    flex-direction: row;            /* default value; can be omitted */
    flex-wrap: nowrap; 
    height: 25px;
    width: 100%;
    background-color: #000888;
    top: 0;
  }

  
  .content{
      padding-left: 5px;
  }

  .application{
    height: 0px;
    width: 0px;
    position: absolute;
  }